import React, { useEffect, useRef } from "react"
import PropTypes from "prop-types"
import { FaArrowLeft, FaHome } from "react-icons/fa"
import { navigate } from "gatsby"

const Header = ({ title = "" }) => {
  console.log(navigate)
  const home = useRef()
  const back = useRef()

  useEffect(() => {
    if (document.referrer) {
      back.current.classList.remove("hidden")
    } else {
      home.current.classList.remove("hidden")
    }
  }, [])

  return (
    <header className="text-white py-4 bg-primary-500">
      <nav className="size__container flex justify-between items-center text-lg">
        <div className="px-2 text-3xl">
          <button className="hidden" ref={back} onClick={() => navigate(-1)}>
            <FaArrowLeft />
          </button>
          <button className="hidden" ref={home} onClick={() => navigate("/")}>
            <FaHome />
          </button>
        </div>

        <h1>{title}</h1>
        <span></span>
      </nav>
    </header>
  )
}

Header.propTypes = {
  title: PropTypes.string,
}

export default Header
